
export class Solution {
  div: HTMLElement;
  renderer: RenderWireframe;
  pts = []
  t = 0.0;
  constructor(id: string) {
    this.div = document.getElementById(id);

    this.renderer = new RenderWireframe(id);

    this.pts =  genRandomPt();
    this.renderer.pts = this.pts;
    

    this.upadte();
  }
  public upadte () {
    requestAnimationFrame(() => { this.upadte(); });


    for(let i = 0 ; i < this.pts.length; ++i) {
        this.pts[i].z += Math.cos( (this.pts[i].x + this.pts[i].y) * this.t) * 2
    }

    this.t += 0.001;
  }
  public destroy() {
    // this.renderer.destroy();
    try {
      while (this.div && this.div.lastElementChild) {
        const lastChild = this.div.lastElementChild as Node;
        this.div.removeChild(lastChild);
      }
    } catch (error) {
      console.error("Error removing child elements:", error);
    }
  }
}


const genRandomPt = (numPoints = 100) => {
    const pts = []
    for (let i = 0; i < numPoints; i++) {
        pts.push({
        x: (Math.random() - 0.5) * 500,
        y: (Math.random() - 0.5) * 500,
        z: (Math.random() - 0.5) * 500,
      });
    }
    return pts;
  };
  
const genGridPt = (numPoints = 5) => {
    const scale = 10;
    const pts = []
    for (let j = -numPoints ; j < numPoints; j++) {
      for (let i = -numPoints; i < numPoints; i++) {
        pts.push({
          x: i * scale,
          y: j * scale,  // Z-axis is up, so keep Y constant (ground plane)
          z: 0,
        });
      }
    }
    return pts;
}


class RenderWireframe {
    pts = []
    constructor(id) {
      const div = document.getElementById(id);
      const bounding = div.getBoundingClientRect();
      const canvas = document.createElement("canvas");
      canvas.width = bounding.width;
      canvas.height = bounding.height;
      div.appendChild(canvas);
  
      const ctx = canvas.getContext("2d");
  
      let angleX = -Math.PI / 2;
      let angleY = 0;
      let dragging = false;
      let panning = false;
      let zoom = 1;
      let lastMouseX = 0;
      let lastMouseY = 0;
      let offsetX = 0;
      let offsetY = 0;
      let panStartX = 0;
      let panStartY = 0;
      let fov = 500;

  
      const rotate = (point, angleX, angleY) => {
        const cosX = Math.cos(angleX);
        const sinX = Math.sin(angleX);
        const cosY = Math.cos(angleY);
        const sinY = Math.sin(angleY);
  
        // (swap Y and Z for Z-up)
        let x = point.x * cosY - point.y * sinY;
        let y = point.x * sinY + point.y * cosY;
  
        let z = point.z * cosX - y * sinX;
        y = point.z * sinX + y * cosX;
  
        return { x, y, z };
      }
  
      const project = (point) => {
        const scale = (fov / (fov + point.y)) * zoom;  // Z-axis is now vertical
        return {
          x: point.x * scale + canvas.width / 2 + offsetX,
          y: -point.z * scale + canvas.height / 2 + offsetY,  // Invert Z for canvas Y-axis
        };
      }
  
      const rendering = () => {
        requestAnimationFrame(() => { rendering(); });

        ctx.clearRect(0, 0, canvas.width, canvas.height);
  
        this.pts.forEach((point) => {
          const rotated = rotate(point, angleX, angleY);
          const projected = project(rotated);
  
          const depthScale = (fov / (fov + rotated.y)) * zoom;
          let radius = 2 * depthScale;
          radius = radius < 0.1 ? 0.1 : radius;
  
          ctx.beginPath();
          ctx.arc(projected.x, projected.y, radius, 0, Math.PI * 2);
          ctx.fillStyle = "#f00";
          ctx.fill();
        });
      }
  

      canvas.addEventListener("mousedown", (e) => {
        if (e.button === 0) {
          dragging = true;
        } else if (e.button === 2) {
          panning = true;
          panStartX = e.clientX - offsetX;
          panStartY = e.clientY - offsetY;
        }
        lastMouseX = e.clientX;
        lastMouseY = e.clientY;
      });

      canvas.addEventListener("mousemove", (e) => {
        if (dragging) {
          const dx = e.clientX - lastMouseX;
          const dy = e.clientY - lastMouseY;
          angleX -= dy * 0.01;
          angleY += dx * 0.01;
          lastMouseX = e.clientX;
          lastMouseY = e.clientY;
          rendering();
        } else if (panning) {
          offsetX = e.clientX - panStartX;
          offsetY = e.clientY - panStartY;
          rendering();
        }
      });
  
      canvas.addEventListener("mouseup", () => {
        dragging = false;
        panning = false;
      });
  
      canvas.addEventListener("contextmenu", (e) => {
        e.preventDefault();
      });
  
      canvas.addEventListener("wheel", (e) => {
        zoom += e.deltaY * -0.001;
        zoom = Math.min(Math.max(zoom, 0.1), 50);
        rendering();
      });
  
      rendering();
    }

}